<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" clearable v-model.trim="queryInfo.query" @clear="getOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="queryInfo.query?getOrdersList():''"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" border style="width: 100%" :stripe="true">
        <el-table-column type="index" label="#" align="center"></el-table-column>
        <el-table-column prop="order_number" label="" align="center"></el-table-column>
        <el-table-column prop="order_price" label="" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            {{scope.row.create_time*1000 | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditAddrDialog(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button type="success" icon="el-icon-location" size="mini" @click="showProcessDialog(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background>
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editAddrForm" :rules="addrFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="/" prop="addr1">
          <!--  -->
          <!-- options -->
          <!-- props -->
          <el-cascader
          v-model="editAddrForm.addr1"
          :options="cityData"
          :props="cityProps"
          clearable>
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="addr2">
          <el-input v-model.trim="editAddrForm.addr2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editDialogVisible=false"> </el-button>
        <el-button type="primary" @click="editAddr"> </el-button>
      </span>
    </el-dialog>
     <!--  -->
    <el-dialog title="" :visible.sync="processDialogVisible" width="50%">
      <!--  -->
      <!-- <el-timeline :reverse="true">
        <el-timeline-item v-for="(item, index) in processList" :key="index" :timestamp="item.timestamp">
          {{item.content}}
        </el-timeline-item>
      </el-timeline> -->
    </el-dialog>
  </div>
</template>
<script>
import cityData from './citydata.js'
export default {
  data () {
    return {
      queryInfo: {
        query: '', // 
        pagenum: 1, // 
        pagesize: 2 // 
      },
      // 
      ordersList: [],
      // 
      total: 0,
      // /
      editDialogVisible: false,
      // 
      editAddrForm: {
        addr1: [],
        addr2: ''
      },
      // 
      cityData,
      cityProps: {
        // 
        expandTrigger: 'hover'
      },
      // 
      addrFormRules: {
        addr1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        addr2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // /
      processDialogVisible: false,
      // 
      processList: [
        {
          timestamp: '',
          content: '2021-05-20 10:30'
        },
        {
          timestamp: '',
          content: '2021-05-20 17:25'
        },
        {
          timestamp: '',
          content: '2021-05-20 16:45'
        }
      ]
    }
  },
  created () {
    this.getOrdersList()
  },
  methods: {
    async getOrdersList () {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: '',
          center: true,
          duration: 1000
        })
      }
      // 
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange (newPagesize) {
      this.queryInfo.pagesize = newPagesize
      this.getOrdersList()
    },
    // 
    handleCurrentChange (newPagenum) {
      this.queryInfo.pagenum = newPagenum
      this.getOrdersList()
    },
    // 
    showEditAddrDialog () {
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      // 
      this.$refs.editFormRef.resetFields()
    },
    // 
    editAddr () {
      this.$refs.editFormRef.validate((valid) => {
        if (!valid) return false
        // 
        const addrData = this.editAddrForm.addr1.join('') + this.editAddrForm.addr2
        this.$message({
          type: 'success',
          message: `${addrData}`
        })
        this.editDialogVisible = false
      })
    },
    // 
    async showProcessDialog () {
      // const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      // console.log(res)
      // if (res.meta.status !== 200) {
      //   return this.$message({
      //     type: 'error',
      //     message: '',
      //     center: true,
      //     duration: 1000
      //   })
      // }
      // this.processList = res.data
      this.processDialogVisible = true
    }
  }
}
</script>
<style lang="less" scoped>
  .el-cascader{
    width: 100%;
  }
</style>
